@import url('http://fonts.cdnfonts.com/css/neue-haas-grotesk-display-pro');

.container{
    color: lightcoral;
   position: absolute;
   z-index: -5;
   font-size: 9em;
   font-weight: 600;
   opacity: 0.7;
   font-family: 'Neue Haas Grotesk Display Pro', sans-serif;
   -webkit-animation:spin 20s linear infinite;
   -moz-animation:spin 20s linear infinite;
   animation:spin 30s linear infinite;
   text-shadow: 5px 10px 10px black;
   
}
@-moz-keyframes spin { 
    100% { -moz-transform: rotate(360deg); } 
}
@-webkit-keyframes spin { 
    100% { -webkit-transform: rotate(360deg); } 
}
@keyframes spin { 
    100% { 
        -webkit-transform: rotate(360deg); 
        transform:rotate(360deg); 
    } 
}
.hero-section{
    background:
    linear-gradient(
        rgba(0,0,0,0.1),
          rgba(0,0,0,0.9)
    ),
    url('./Images/toyko2.gif') center ;
    background-color: black;
    background-size: contain;
    height: 91.6vh;
    min-height: 91.5vh;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
}

.number-header{
    display: block;
    float: left;
    position: relative;
    transform: translate(90px,-235px);
    font-weight: 70;
    color:white;
    font-size: 15em;
    font-family: 'Neue Haas Grotesk Display Pro', sans-serif;
    height: 50vh;
    width: 50vh;
    text-shadow: 10px 10px 10px black;
    z-index: 1;
}
.logo{
    height: 170%;
    filter: drop-shadow(30px 30px 30rem black);
}
.logo2{
    height: 30%;
}

.bg{
    position: absolute;
    z-index: -3;
    transform: translate(-100px,-280px);
}

.bg-info{
    position: absolute;
    z-index: -3;
    transform: translate(-330px,420px);
    width: 37%;
    height: 37%;
}
.bg-under-info{
    position: absolute;
    z-index: -7;
    transform: translate(0px,0);
    opacity: 0.75;
    height: 100%;
}
.info-under-header{
    display: flex;
    position: relative;
    font-family: "Simplon Norm","Helvetica Neue","Helvetica","Arial",sans-serif;
    line-height: 1;
    opacity: 1;
    font-size: 2.75rem;
    text-shadow: 30px 30px 30px black;
    transform: translate(-300px,470px);
    color: white;
    text-align: center;
    width: 32%;
    z-index: 1;
}

