.image1{
    height: 400px;
    filter: brightness(80%);
    filter: drop-shadow(0 0 0.75rem black);
    z-index: -1;
}

.image2,.image3{
    height:400px;
    filter: brightness(70%);
    filter: drop-shadow(0 0 0.75rem black);
    z-index: -1;
    
}

.right-content-stack{
    display: block;
    position: flex;
    justify-content: right;
    transform: translate(0,15px);
    margin-right: 0%;
    float:right;
    width:43%;
}
.hero-info{
    font-size: 1.7em;
    width: 65%;
    color:white;
    font-weight: 100;
    font-family: 'Neue Haas Grotesk Display Pro', sans-serif;
}

.images{
    display: flex;
    position: relative;
    width: 100px;
    height:100%;
    margin-top: 5%;
    
    
}
.image-stack__item--bottom{
    position: absolute;
    z-index: -3;
    transition: all 0.2s ease-in;
}



.image-stack__item--top{
    position: absolute;
    left: 60%;
    top: 20px;
    z-index: -2;
    transition: all 0.2s ease-in;
}
.image-stack__item--top2{
    position: absolute;
    top: 40px;
    left: 120%;
    transition: all 0.2s ease-in;

}

.image-stack__item--top3{
    position: absolute;
    top: 60px;
    left: 170%;
    transition: all 0.2s ease-in;

}

.image-stack__item--top4{
    position: absolute;
    top: 80px;
    left: 220%;
    transition: all 0.2s ease-in;

}
.image-stack__item--bottom:hover,.image-stack__item--top:hover,.image-stack__item--top2:hover,.image-stack__item--top3:hover,.image-stack__item--top4:hover{
    transform: translate(0,-30px);
}

.bottom-hr{
    width: 63%;
    transform: translate(80px,440px);
}


.fade-in-image{
    animation:instant 0.5s;
}
.fade-in-content{
    animation:fadeIn 0.7s;
}

.fade-in-image2{
    animation:instant 0.7s;
}
.fade-in-image3{
    animation: instant 1s;
}

.fade-in-image4{
    animation: instant 1.1s;
}

@keyframes instant{
    0%{
        opacity: 0;
    }
    99%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}



.desc{
    font-weight: 500;
    height: 8vh;
}

