}

.images{
    display: flex;
    position: relative;
    width: 100px;
    height:100%;
    margin-top: 5%;
    
    
}
.image-stack__item--bottom{
    position: absolute;
    z-index: -3;
    transition: all 0.2s ease-in;
}



.image-stack__item--top{
    position: absolute;
    left: 60%;
    top: 20px;
    z-index: -2;
    transition: all 0.2s ease-in;
}
.image-stack__item--top2{
    position: absolute;
    top: 40px;
    left: 120%;
    transition: all 0.2s ease-in;

}

.image-stack__item--top3{
    position: absolute;
    top: 60px;
    left: 170%;
    transition: all 0.2s ease-in;

}

.image-stack__item--top4{
    position: absolute;
    top: 80px;
    left: 220%;
    transition: all 0.2s ease-in;

}
.image-stack__item--bottom:hover,.image-stack__item--top:hover,.image-stack__item--top2:hover,.image-stack__item--top3:hover,.image-stack__item--top4:hover{
    -webkit-transform: translate(0,-30px);
            transform: translate(0,-30px);
}

.bottom-hr{
    width: 63%;
    -webkit-transform: translate(80px,440px);
            transform: translate(80px,440px);
}


.fade-in-image{
    -webkit-animation:instant 0.5s;
            animation:instant 0.5s;
}
.fade-in-content{
    -webkit-animation:fadeIn 0.7s;
            animation:fadeIn 0.7s;
}

.fade-in-image2{
    -webkit-animation:instant 0.7s;
            animation:instant 0.7s;
}
.fade-in-image3{
    -webkit-animation: instant 1s;
            animation: instant 1s;
}

.fade-in-image4{
    -webkit-animation: instant 1.1s;
            animation: instant 1.1s;
}

@-webkit-keyframes instant{
    0%{
        opacity: 0;
    }
    99%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes instant{
    0%{
        opacity: 0;
    }
    99%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn{
